import { Image, TreeSelect } from "@taroify/core"
import Block from "../../../components/block"
import Page from "../../../components/page"

import "./index.scss"

function RadioTreeSelect() {
  return (
    <TreeSelect>
      <TreeSelect.Tab title="">
        <TreeSelect.Option value={0}></TreeSelect.Option>
        <TreeSelect.Option value={1}></TreeSelect.Option>
        <TreeSelect.Option value={2} disabled>
          
        </TreeSelect.Option>
        <TreeSelect.Option value={3}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab title="">
        <TreeSelect.Option value={4}></TreeSelect.Option>
        <TreeSelect.Option value={5}></TreeSelect.Option>
        <TreeSelect.Option value={6}></TreeSelect.Option>
        <TreeSelect.Option value={7}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab title="" disabled>
        <TreeSelect.Option value={8}></TreeSelect.Option>
        <TreeSelect.Option value={9}></TreeSelect.Option>
      </TreeSelect.Tab>
    </TreeSelect>
  )
}

function MultiselectTreeSelect() {
  return (
    <TreeSelect defaultValue={[0, 1]}>
      <TreeSelect.Tab title="">
        <TreeSelect.Option value={0}></TreeSelect.Option>
        <TreeSelect.Option value={1}></TreeSelect.Option>
        <TreeSelect.Option value={2} disabled>
          
        </TreeSelect.Option>
        <TreeSelect.Option value={3}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab title="">
        <TreeSelect.Option value={4}></TreeSelect.Option>
        <TreeSelect.Option value={5}></TreeSelect.Option>
        <TreeSelect.Option value={6}></TreeSelect.Option>
        <TreeSelect.Option value={7}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab title="" disabled>
        <TreeSelect.Option value={8}></TreeSelect.Option>
        <TreeSelect.Option value={9}></TreeSelect.Option>
      </TreeSelect.Tab>
    </TreeSelect>
  )
}

function TreeSelectWithCustomContent() {
  return (
    <TreeSelect>
      <TreeSelect.Tab title=" 1">
        <Image src="https://img.yzcdn.cn/vant/apple-1.jpg" />
      </TreeSelect.Tab>
      <TreeSelect.Tab title=" 2">
        <Image src="https://img.yzcdn.cn/vant/apple-2.jpg" />
      </TreeSelect.Tab>
    </TreeSelect>
  )
}

function BadgeTreeSelect() {
  return (
    <TreeSelect>
      <TreeSelect.Tab badge title="">
        <TreeSelect.Option value={0}></TreeSelect.Option>
        <TreeSelect.Option value={1}></TreeSelect.Option>
        <TreeSelect.Option value={2} disabled>
          
        </TreeSelect.Option>
        <TreeSelect.Option value={3}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab badge="5" title="">
        <TreeSelect.Option value={4}></TreeSelect.Option>
        <TreeSelect.Option value={5}></TreeSelect.Option>
        <TreeSelect.Option value={6}></TreeSelect.Option>
        <TreeSelect.Option value={7}></TreeSelect.Option>
      </TreeSelect.Tab>
      <TreeSelect.Tab title="" disabled>
        <TreeSelect.Option value={8}></TreeSelect.Option>
        <TreeSelect.Option value={9}></TreeSelect.Option>n
      </TreeSelect.Tab>
    </TreeSelect>
  )
}

export default function TreeSelectDemo() {
  return (
    <Page title="TreeSelect " className="tree-select-demo">
      <Block title="">
        <RadioTreeSelect />
      </Block>
      <Block title="">
        <MultiselectTreeSelect />
      </Block>
      <Block title="">
        <TreeSelectWithCustomContent />
      </Block>
      <Block title="">
        <BadgeTreeSelect />
      </Block>
    </Page>
  )
}
